// import 'gitalk/dist/gitalk.css';
import Gitalk from 'gitalk';

import React from 'react';
import ReactGA from 'react-ga';
import Head from 'next/head';
import Router from 'next/router';
import Link from 'next/link';
import Button from 'material-ui/Button';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
// import NavigateBefore from '@material-ui/icons/NavigateBefore';
import Layout from '../comps/BlogLayout';
import P from '../comps/P';
import InlineCode from '../comps/InlineCode';
// import AppBar from '../comps/AppBar';

export default function WithDoc(options) {
  const goTopHandler = () =>
    Router.push(`/blog/${options.slug}`).then(() => {
      let verPos = window.scrollY;
      // console.log(verPos);
      const tmpPos = setInterval(() => {
        if (verPos >= 80) {
          window.scrollTo(0, verPos);
          verPos -= 80;
        } else {
          window.scrollTo(0, 0);
          // verPos -= 1;
          clearInterval(tmpPos);
        }
        // if (verPos === 0) clearInterval(tmpPos);
      }, 1);
    });
  // const goBackHandler = () =>
  //   Router.push(`/blog`).then(() => window.scrollTo(0, 0));
  // eslint-disable-next-line func-names

  function funcRet(content) {
    return class BlogPost extends React.Component {
      componentDidMount() {
        ReactGA.initialize(options.GATrackID);
        ReactGA.pageview(window.location.pathname + window.location.search);
        // console.log('heyheyhey from blog post------------->>>>>>>>>>');
        // console.log(window.location.pathname);
        // console.log(window.location.search);
        // console.log(window.location.pathname + window.location.search);

        const gitalk = new Gitalk({
          clientID: '4c6a348acd48313512cd',
          clientSecret: 'b5d87fd63766422f6c54076204df828d4df656bc',
          repo: 'PharrellWANG.github.io',
          owner: 'PharrellWANG',
          admin: ['PharrellWANG'],
          id: options.slug, // Ensure uniqueness and length less than 50
          distractionFreeMode: true, // Facebook-like distraction free mode
        });

        gitalk.render('gitalk-container');
      }
      render() {
        return (
          <Layout>
            <Head>
              <title>{options.title}</title>
              <meta name="description" content={options.description} />
            </Head>
            {/* <AppBar activePageName="Blog" /> */}
            <main className="outerContainer">
              <div className="headerTop">
                <span className="spanPaddingRight">
                  <Link href="/blog" as={`${process.env.URL_PREFIX}/blog`}>
                    <a>&lt;&nbsp;Back</a>
                  </Link>
                  {/* <a href="/blog">Blog</a>&nbsp;&nbsp;&gt;&nbsp;&nbsp; */}
                  {/* {options.title} */}
                </span>
              </div>
              <div className="innerContainer">
                <div className="content">
                  <div className="date">Published on {options.date}</div>
                  <h1>{options.title}</h1>
                  <div className="markdown-content">{content}</div>
                  {/* <Button */}
                  {/* variant="fab" */}
                  {/* color="secondary" */}
                  {/* aria-label="back-to-blog-list" */}
                  {/* // className="floatingButton" */}
                  {/* style={{ */}
                  {/* zIndex: 1800, */}
                  {/* position: 'fixed', */}
                  {/* bottom: 24, */}
                  {/* top: 'auto', */}
                  {/* left: 16, */}
                  {/* right: 'auto', */}
                  {/* }} */}
                  {/* onClick={goBackHandler} */}
                  {/* > */}
                  {/* <NavigateBefore /> */}
                  {/* </Button> */}
                  <Button
                    variant="fab"
                    color="inherit"
                    aria-label="scroll-to-top"
                    // className="floatingButton"
                    style={{
                      zIndex: 1800,
                      position: 'fixed',
                      bottom: 24,
                      top: 'auto',
                      right: 16,
                      left: 'auto',
                    }}
                    onClick={goTopHandler}
                  >
                    <ArrowUpward />
                  </Button>
                  <div id="gitalk-container" />
                </div>
              </div>
              <div className="footerWrapper">
                <div className="copyright">&copy; 2019, Pharrell WANG</div>
              </div>
            </main>
            <style jsx>{`
              .spanPaddingRight {
                display: block;
                margin: 0 auto 0 auto;
                max-width: 650px;
                padding: 0 21px;
              }
              .span {
                padding-left: 30px;
              }
              .backIcon {
                padding-right: 6px;
              }

              a {
                color: #fff;
                text-decoration: none;
                opacity: 1;
              }

              a:hover {
                color: #fefefe;
                text-decoration: none;
                opacity: 0.6;
              }

              .footerWrapper {
                margin-top: 72px;
                // border-top: 1px solid #666;
                background: #f2f2f2;
                height: 120px;
                z-index: 1100;
              }
              .copyright {
                flex: 1;
                // margin-top: 36;
                padding-top: 36px;
                text-align: center;
              }
              .headerTop {
                // flex: 1;
                // box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
                //   0px 4px 5px 0px rgba(0, 0, 0, 0.14),
                //   0px 1px 10px 0px rgba(0, 0, 0, 0.12);
                padding-top: 13px;
                padding-bottom: 13px;
                background-color: #333;
                // border-bottom: 1px solid #8bc34a;
                // border-bottom: 1px solid #212121;
                // top: 0px;
                // z-index: 1800px;
                // width: 100%;
              }
              .outerContainer {
                display: flex;
                min-height: 100vh;
                flex-direction: column;
                background-color: #fafafa;
                width: 100%;
                height: 100%;
                // padding-top: 56;
              }

              .innerContainer {
                padding: 0 21px;
                flex: 1;
              }

              .content {
                margin: 0 auto 0 auto;
                max-width: 650px;
                padding-top: 24px;
              }

              .content h1 {
                // use material-ui headline
                font-size: 1.5rem;
                font-weight: 500;
                font-family: Roboto, Helvetica, Arial, sans-serif;
                line-height: 1.15417em;
                color: rgba(0, 0, 0, 0.54);
              }

              .date {
                color: #888;
                // use material-ui caption
                font-size: 12px;
                padding-top: 3px;
              }

              .markdown-content {
                margin: 50px 0 0 0;
              }
            `}</style>

            <style jsx global>{`
              .markdown-content {
                color: rgba(0, 0, 0, 0.87);
                line-height: 1.46429em;
                font-size: 0.9rem;
                font-weight: 400;
              }

              .markdown-content hr {
                border: 0;
                border-bottom: 1px solid #8bc34a;
                margin: 40px 60px;
              }

              .markdown-content a {
                color: #1e88e5;
                text-decoration: none;
                border-bottom: 1px solid #1e88e5;
              }

              .markdown-content strong {
                font-weight: 500;
                text-decoration: none;
              }

              .markdown-content li {
                margin: 10px 0;
                // font-size: 1rem;
              }

              .markdown-content h2 {
                // use smaller material-ui headline
                font-size: 1.2rem;
                font-weight: 500;
                font-family: Roboto, Helvetica, Arial, sans-serif;
                line-height: 1.15417em;
                color: rgba(0, 0, 0, 0.87);
              }

              .markdown-content h3 {
                // use smaller material-ui headline
                font-size: 1rem;
                font-weight: 500;
                font-family: Roboto, Helvetica, Arial, sans-serif;
                line-height: 1.15417em;
                text-transform: uppercase;
                color: rgba(0, 0, 0, 0.87);
              }

              .markdown-content blockquote {
                padding: 1px 0 1px 15px;
                margin: 0;
                border-left: 5px solid #8bc34a;
              }
            `}</style>
          </Layout>
        );
      }
    };
  }

  return funcRet;
}

export const components = {
  p: P,
  code: InlineCode,
};



